import Axios from "axios";
import { baseUrl } from "../../main";

const state = () => ({
  prompter: undefined,
  prompters: [],
});

const getters = {
  allPrompter: (state) => state.prompter,
  allPrompters: (state) => state.prompters,
};

const actions = {
  async getPrompters({ commit }, data) {
    try {
      const response = await Axios.post(baseUrl + "prompt/promptList", data, {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
      commit("setPrompters", response.data);
    } catch (error) {
      console.log(error);
    }
  },
  async getPrompter({ commit }, data) {
    try {
      const response = await Axios.get(baseUrl + "prompt/prompter/"+ data, {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
      commit("setPrompter", response.data);
    } catch (error) {
      console.log(error);
    }
  },
  async postPrompter({ commit }, data) {
    try {
      const response = await Axios.post(baseUrl + "prompt/create", data, {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
      commit("setPrompters", response.data);
    } catch (error) {
      console.log(error);
    }
  },
  async deletePrompter({ commit }, data) {
    try {
      const response = await Axios.delete(baseUrl + "prompt/prompter/" + data, {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
      commit("setPrompters", response.data);
    } catch (error) {
      console.log(error);
    }
  },
};

const mutations = {
  setPrompter: (state, data) => (state.prompter = data),
  setPrompters: (state, data) => (state.prompters = data),
};

export default { state, getters, actions, mutations };
