import Axios from "axios";
import { baseUrl } from "../../main";

const state = () => ({
  report: undefined,
  reports: [],
});

const getters = {
  allReport: (state) => state.report,
  allReports: (state) => state.reports,
};

const actions = {
  async getReports({ commit },data) {
    try {
      const response = await Axios.post(baseUrl + "report/reportList", data,{
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
      commit("setReports", response.data);
    } catch (error) {
      console.log(error);
    }
  },

};

const mutations = {
  setReport: (state, data) => (state.report = data),
  setReports: (state, data) => (state.reports = data),
};

export default { state, getters, actions, mutations };
