import Axios from "axios";
import { baseUrl } from "../../main";

const state = () => ({
  challenege: undefined,
  challeneges: [],
});

const getters = {
  allChallenge: (state) => state.challenege,
  allChallenges: (state) => state.challeneges,
};

const actions = {
  async postChallenge({ commit }, data) {
    try {
      const response = await Axios.post(baseUrl + "challenge/create", data, {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
   
      commit("setChallenge", response.data.results);
    } catch (error) {
      console.log(error);
    }
  },
  async getChallenge({ commit }, data) {
    try {
      const response = await Axios.get(
        baseUrl + "challenge/challengeDash/" + data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setChallenge", response.data.results);
    } catch (error) {
      console.log(error);
    }
  },
  async postChallengeQuestion({ commit }, data) {
    try {
      const response = await Axios.post(
        baseUrl + "challenge/createQuestion",
        data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setChallenge", response.data.results);
    } catch (error) {
      console.log(error);
    }
  },
  async deleteChallengeQuestion({ commit }, data) {
    try {
      const response = await Axios.delete(
        baseUrl + "challenge/question/" + data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setChallenge", response.data.results);
    } catch (error) {
      console.log(error);
    }
  },
  async updateChallengeQuestion({ commit }, data) {
    try {
      const response = await Axios.put(
        baseUrl + "challenge/question",
        data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setChallenge", response.data.results);
    } catch (error) {
      console.log(error);
    }
  },
  async getChallenges({ commit }, data) {
    try {
      const response = await Axios.post(
        baseUrl + "challenge/challegeList",
        data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setChallenges", response.data);
    } catch (error) {
      console.log(error);
    }
  },
};

const mutations = {
  setChallenge: (state, data) => (state.challenege = data),
  setChallenges: (state, data) => (state.challeneges = data),
};

export default { state, getters, actions, mutations };
