import Axios from "axios";
import { baseUrl } from "../../main";

const state = () => ({
  notification: undefined,
  notifications: [],
});

const getters = {
  allNotification: (state) => state.notification,
  allNotifications: (state) => state.notifications,
};

const actions = {
  async getNotifications({ commit }, data) {
    try {
      const response = await Axios.post(
        baseUrl + "notification/notificationHistoryAdmin",
        data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setNotifications", response.data);
    } catch (error) {
      console.log(error);
    }
  },
  async postNotification({ commit }, data) {
    try {
      const response = await Axios.post(baseUrl + "notification/create", data, {
        headers: {
          Authorization:
            "Bearer " + JSON.parse(localStorage.getItem("token")).token,
        },
      });
      commit("setNotifications", response.data);
    } catch (error) {
      console.log(error);
    }
  },
  async getNotification({ commit }, data) {
    try {
      const response = await Axios.get(
        baseUrl + "notification/sendnotificationall/" + data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setNotification", response.data);
    } catch (error) {
      console.log(error);
    }
  },
  async deleteNotification({ commit }, data) {
    try {
      const response = await Axios.delete(
        baseUrl + "notification/notificationdelete/" + data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setNotifications", response.data);
    } catch (error) {
      console.log(error);
    }
  },
};

const mutations = {
  setNotification: (state, data) => (state.notification = data),
  setNotifications: (state, data) => (state.notifications = data),
};

export default { state, getters, actions, mutations };
