import Axios from "axios";
import { baseUrl } from "../../main";

const state = () => ({
  community: undefined,
  communities: [],
});

const getters = {
  allCommunity: (state) => state.community,
  allCommunities: (state) => state.communities,
};

const actions = {
  async getCommunities({ commit }, data) {
    try {
      const response = await Axios.post(
        baseUrl + "community/communityList",
        data,
        {
          headers: {
            Authorization:
              "Bearer " + JSON.parse(localStorage.getItem("token")).token,
          },
        }
      );
      commit("setCommunities", response.data);
    } catch (error) {
      console.log(error);
    }
  },
};

const mutations = {
  setCommunity: (state, data) => (state.community = data),
  setCommunities: (state, data) => (state.communities = data),
};

export default { state, getters, actions, mutations };
